import Send from "@carbon/icons-react/es/Send.js";

import React, { useCallback } from "react";

import { useServiceManager } from "../../../hooks/useServiceManager.js";

import actions from "../../../store/actions.js";

import { WA_CONSOLE_PREFIX } from "../../../utils/constants.js";

import { createMessageRequestForButtonItemOption } from "../../../utils/messageUtils.js";

import { consoleError } from "../../../utils/miscUtils.js";

import { MessageSendSource } from "../../../../../types/events/eventBusTypes.js";

import { BaseButtonItemComponent } from "./BaseButtonItemComponent.js";

